#wpwrap {
	background-color: #f2f7fb;
}

#simcal-settings-page-form .select2-selection {
	border-radius: 5px;
}

#simcal-settings-page-form .nav-tab-active,
#simcal-settings-page-form .nav-tab-active:focus,
#simcal-settings-page-form .nav-tab-active:hover {
	background: none;
	border-bottom: 3px solid #60bc4e;
}

#simcal-settings-page-form .nav-tab-active:active {
	color: #3a414c;
}

#simcal-settings-page-form .sc-link:hover {
	color: #60bc4e;
}

#simcal-settings-page-form a:active {
	color: #3a414c;
}

#simcal-settings-page-form span.simcal-field-bool {
	margin-left: 155px;
}

.semical-feeds-tab .form-table th,
.semical-calendars-tab .form-table th,
.semical-licenses-tab .form-table th {
	width: 100%;
	display: block;
	font-size: 16px;
	font-weight: 400;
	line-height: 24px;
	font-family: 'poppins', sans-serif;
}

.semical-feeds-tab .form-table td,
.semical-calendars-tab .form-table td,
.semical-licenses-tab .form-table td {
	display: block;
	padding: unset;
}

#simcal-settings-page-form .form-table th {
	padding: 14px 14px 14px 0px;
}

#simcal-settings-page-form .button-primary {
	background-color: #60bc4e;
	font-size: 16px;
	border-color: unset;
	border-radius: 7px;
	width: 150px;
	height: 40px;
}

#simcal-settings-page-form .button-primary:focus {
	box-shadow: unset;
}

#simcal-settings-page-form .submit {
	padding: unset;
}

#simcal-settings-page-form .form-table,
#simcal-settings-page-form .form-table td,
#simcal-settings-page-form .form-table th h2 {
	font-size: 16px;
}

#simcal-settings-page-form .form-table th {
	font-weight: 400;
	font-size: 16px;
}

#simcal-settings-page-form .nav-tab-wrapper,
#simcal-settings-page-form .wrap h2.nav-tab-wrapper,
#simcal-settings-page-form h1.nav-tab-wrapper {
	border-bottom: unset;
}

#simcal-settings-page-form .wp-person a:focus .gravatar,
a:focus,
a:focus .media-icon img,
a:focus .plugin-icon {
	box-shadow: unset;
}

#simcal-settings-page-form .simcal-icon-help:before {
	color: #60bc4e;
	font-size: 20px;
	margin-left: 5px;
}

#simcal-settings-page-form .simcal-field-bool > input {
	line-height: unset;
	margin-right: 10px;
}

#simcal-settings-page-form div#setting-error-settings_updated {
	max-width: 100%;
	margin: 1% 5% 0 5%;
}

#simcal-settings-page-form .notice-error {
	max-width: 100%;
	margin: 1% 0 0 5%;
}

#simcal-settings-page-form a:active {
	color: #60bc4e;
}

#simcal-settings-page-form a.button.button-secondary {
	background: #60bc4e;
	color: white;
	border: unset;
}

#simcal-settings-page-form a.button.button-secondary:hover {
	background: #60bc4e;
	color: white;
	border: unset;
}

#simcal-settings-page-form .simcal-wide-text {
	width: 100%;
}

#simcal-settings-page-form .select2 {
	max-width: 95%;
}

#simcal-settings-page-form .sc-chk-box:checked::before {
	content: url('../images/green-tick-check.png');
	margin: 0px;
	margin-top: -1px;
	margin-left: -1px;
	border: none;
}

#simcal-settings-page-form .sc-chk-box {
	height: 19.5px;
	width: 19.5px;
	margin-left: -1px;
}

#simcal-settings-page-form h2,
#simcal-settings-page-form h3 {
	color: #3a414c;
	font-size: 20px;
	font-weight: 600;
}

#simcal-settings-page-form .form-table th,
#simcal-settings-page-form .form-wrap label {
	color: #3a414c;
}

#simcal-settings-page-form .wp-core-ui .button-primary.active,
#simcal-settings-page-form .wp-core-ui .button-primary.active:focus,
#simcal-settings-page-form .wp-core-ui .button-primary.active:hover,
#simcal-settings-page-form .wp-core-ui .button-primary:active {
	border: unset;
	background: unset;
}

#simcal-settings-page-form .simcal-addon-manage-license-buttons {
	margin-top: 20px;
}

#simcal-settings-page-form button.button-secondary.simcal-addon-manage-license.activate,
#simcal-settings-page-form button.button-secondary.simcal-addon-manage-license.deactivate {
	background-color: #60bc4e;
	color: white;
	border: unset;
}

#simcal-settings-page-form .form-table {
	border-collapse: unset;
}

#simcal-settings-page-form .regular-text {
	width: 90%;
}

#simcal-settings-page-form .notice.notice-error.is-dismissible {
	max-width: 1302px;
	margin: 1% 0 0 5%;
}

#simcal-settings-page-form .wp-core-ui .button-secondary:focus,
#simcal-settings-page-form .wp-core-ui .button.focus,
#simcal-settings-page-form .wp-core-ui .button:focus {
	box-shadow: unset;
}

#simcal-settings-page .select2-container--open .select2-selection {
	box-shadow: 0 0 2px #d4d4d8;
	border-color: #71717a;
}

#simcal-settings-page-form .sc-error-notice {
	display: inherit;
	margin-top: 25px;
	padding: 20px 5px 0px 0px;
	text-decoration: blink;
	-webkit-animation-name: blinker;
	-webkit-animation-duration: 0.6s;
	-webkit-animation-iteration-count: infinite;
	-webkit-animation-timing-function: ease-in-out;
	-webkit-animation-direction: alternate;
}

@-webkit-keyframes blinker {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}

#simcal-settings-page-form .sc-btn-input {
	box-shadow: 0 0 0 transparent;
	border-radius: 5px;
	border: 1px solid #d4d4d8;
	background-color: #fff;
	color: #71717a;
	height: 40px;
	width: 99%;
}

#simcal-settings-page-form .select2-container .select2-selection--multiple .select2-selection__rendered {
	display: unset;
	height: 40px;
	border-radius: 5px;
}

#simcal-settings-page-form .select2-container .select2-selection--multiple {
	height: 40px;
	border-radius: 5px;
}

#simcal-settings-page-form .sc-chk-box:focus {
	box-shadow: unset;
	border-color: unset;
	outline: unset;
	margin-left: -1px;
}

#simcal-settings-page-form .select2-container--default .select2-selection--multiple .select2-selection__choice {
	margin-top: 8px;
}

#simcal-settings-page-form a.button.button-secondary.sc-btn-suth {
	margin-bottom: 5px;
}

/* media query */
@media only screen and (max-width: 1765px) {
	#simcal-settings-page .select2 {
		max-width: 93%;
	}
}

@media only screen and (max-width: 1025px) {
	#simcal-settings-page .select2 {
		max-width: 90%;
	}
}
